<template>
    <transition name="fade">
      <div class="modal-wrap" v-if="isModalOpened == true"> <!-- v-if는 해당 div가 조건에 부합할때만 show: 이게 기본 전제임 -->
        <div class="modal-body">
          <img :src="onerooms[clickedIndex].image"/>
          <h4>{{ onerooms[clickedIndex].title }}</h4>
          <p>{{ onerooms[clickedIndex].content }}</p>
          <form>
            <input v-model="month"> <!-- @는 할때마다 라는 숙어로 해석하면됨 / v-model은 html데이터를 data로 전달-->
            <!-- $event는 event를 동작시킬 요소이면서, target 현재 이벤트가 일어나는 대상(연결)이면서, value 그의 (값) -->
          </form>
          <p>{{ month }}개월 선택함 : {{ onerooms[clickedIndex].price * month}}</p>
          <button @click="$emit('closeModal')">닫기</button>
        </div>
      </div>
  </transition>
</template>

<script>


export default {
    name : 'Modal',
    props : { // 컴포넌트에서 사용되는 데이터바인딩들을 수입하는 절차 props는 read-only라서 수입한 후에 수정하면 큰일남
        onerooms: Array,// 수출쪽에서 설정한 품목명(작명된 data)과 데이터 유형
        clickedIndex : Number,
        isModalOpened : Boolean,
    },
    data () {
      return {
        month: 1,
      }
    },
    methods: {

    },
    beforeUpdate () {
        if(this.month >= 3) {
          alert('3이상 입력하지마셈')
          this.month = 1
        }
    }
}
</script>

<style>
  /* modal */

.modal-wrap {
  width: auto;
  height: auto;
  background: rgba(0,0,0,0.5);
  position: fixed;
  padding: 20px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-sizing: border-box;
}

.fade-enter-from {
  scale: 0;
}

.fade-enter-active {
  transition: all 1s;
}

.fade-enter-to {
  scale: 1;
}

.modal-body {
  width: 100%;
  height: 100%;
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
}

.modal-body button {
  cursor: pointer;
}
</style>