      price3: '만원',
      */
      showDiscount : true,
      oneroomsOriginal: [...oneroomData], //기본값을 저장해둠 - 근데 같은 데이터를 동시에 사용할땐 [...data]처럼 사본을 생성해야 충돌하지않음
      clickedIndex: 0,
