      isModalOpened: false,
      reportCnt: [0, 0, 0],
      navMenus: ['Home', 'Shop', 'About'],
      products: ['역삼역 원룸', '신림역 원룸', '종각역 원룸'],
      prices: ['1,000만원', '4,000만원', '10,000원'],
