    sortBack() {
      this.onerooms = [...this.oneroomsOriginal]; // this.oneroomsOriginal 로 하면 array=array는 할당이 아니라 공유로 인식함
    },

    priceSort() { //sort(function(a,b)){return a-b}, Sort함수의 동작 원리는 인자값 a와b 를 빼서 음수는 왼쪽 양수는 오른쪽으로 배치하도록 구현
      this.onerooms.sort(function (a, b) {
        return a.price - b.price;
      })
    }
