<template>
   <div class="discount">
    지금 구매하면 {{ discountNum }}% 할인
   </div>
</template>

<script>

export default {
  name: 'Discount',
  data () {
    return {
      discountNum : 30,
    }
  },

  mounted() {
    let timer = setInterval(()=>{
      this.discountNum--
      if (this.discountNum == 0) {
        clearInterval(timer)
      }
    }, 1000)

  }
}
</script>

<style>
.discount {
  background: #eee;
  padding: 20px;
  margin: 10px;

}
</style>
